 body {
	 background-color: #ffdf8c;
}
.cart_wrapper {
  border-radius: 10px;
  max-width: 1400px;
}

 .content {
	 padding: 40px 0;
	 position: relative;
}
 .content__title {
	 margin: 35px 0;
}
 .content__items {
	 display: flex;
	 flex-wrap: wrap;
	 justify-content: space-between;
}
 .content__top {
	 display: flex;
	 align-items: center;
	 justify-content: space-between;
}
 .container {
	 max-width: 1340px;
	 margin: 0 auto;
	}
	.container--cart {
	 height: auto;
	 max-width: 820px;
	 margin-top: 90px;
}
 .container--cart .content__title {
	 margin: 0;
}
 .cart__top {
	 display: flex;
	 justify-content: space-between;
	 align-items: center;
}
 .cart .content__title {
	 display: flex;
	 align-items: center;
	 font-size: 32px;
}
 .cart .content__title svg {
	 position: relative;
	 top: -2px;
	 width: 30px;
	 height: 30px;
	 margin-right: 10px;
}
 .cart .content__title svg path {
	 stroke: #000;
	 stroke-width: 1.9;
}
 .cart__clear {
	 display: flex;
	 align-items: center;
	 cursor: pointer;
}
 .cart__clear span {
	 display: inline-block;
	 margin-left: 7px;
	 color: #b6b6b6;
	 font-size: 18px;
}
 .cart__clear span, .cart__clear svg, .cart__clear path {
	 transition: all 0.5s ease-in-out;
}
 .cart__clear:hover svg path {
	 stroke: #373737;
}
 .cart__clear:hover span {
	 color: #373737;
}
.cart__item {
	display: flex;
	width: 100%;
	padding-top: 30px;
	margin-top: 30px;	 
}
.cart__item-img {
	display: flex;
	align-items: center;
	margin-right: 15px;
	width: 10%;
}
.cart__item-img img {
	width: 80px;
	height: 80px;
}
.cart__item-info {
	display: flex;
	flex-direction: column;
	justify-content: center;
	width: 40%;
}
.cart__item-info h3 {
	font-weight: bold;
	font-size: 22px;
	line-height: 27px;
	letter-spacing: 0.01em;
}
.cart__item-info p {
	font-size: 18px;
	color: #8d8d8d;
}

.cart__item-info h3, p, .cart__item-count b {
	font-family: 'Jost', sans-serif;
}

.cart__item-count {
	display: flex;
	align-items: center;
	justify-content: space-between;
	width: 13%;
}
 .cart__item-count-minus svg path:first-of-type {
	 display: none;
}
 .cart__item-count b {
	 font-size: 22px;
}
 .cart__item-price {
	 display: flex;
	 align-items: center;
	 justify-content: center;
	 width: 33%;
}
 .cart__item-price b {
	 font-weight: bold;
	 font-size: 22px;
	 letter-spacing: 0.01em;
}
 .cart__item-remove {
	 display: flex;
	 align-items: center;
	 justify-content: flex-end;
	 width: 4%;
}
 .cart__item-remove .button {
	 border-color: #676767;
}
 .cart__item-remove svg {
	 -webkit-transform: rotate(45deg);
	         transform: rotate(45deg);
}
 .cart__item-remove svg path {
	 fill: #5a5a5a;
}
 .cart__item-remove .button svg {
	 width: 11.5px;
	 height: 11.5px;
	 position: relative;
}
 .cart__item-remove .button:hover, .cart__item-remove .button:active {
  box-shadow: 0 0 5px #000;
}
 .cart__bottom {
	 margin: 50px 0;
}
 .cart__bottom-details {
	 display: flex;
	 justify-content: space-between;
}
 .cart__bottom-details span {
	 font-size: 22px;
}
 .cart__bottom-details span:last-of-type b {
	 color: orange;
}
 .cart__bottom-buttons {
	 display: flex;
	 justify-content: space-between;
	 margin-top: 40px;
}
 .cart__bottom-buttons .go-back-btn {
	 display: flex;
	 align-items: center;
	 justify-content: center;
	 width: 210px;
   text-decoration: underline;
	 border: 1px solid #D3D3D3;
   border-radius: 30px;
   transition: .3s;
}
 .cart__bottom-buttons .go-back-btn span {
	 color: #d3d3d3;
   text-decoration: underline;
	 font-weight: 600;
}
 .cart__bottom-buttons .go-back-btn:hover {
	 background-color: #000;
	 border-color: #000;
}
 .cart__bottom-buttons .go-back-btn:hover span {
	 color: rgb(255, 255, 255);
}

.cart__bottom-buttons button {
	padding: 10px 18px;
	background-color: #FE5F1E;
	color: #fff;
	font-size: 1em;
